const React = (window as any).React;
const antd = (window as any).antd;

interface Props {
  save_data: (user_data:any)=>void,
  FileUploadConponent: (props: {onUploadSuccess: (file_path: string) => void, title?: string}) => React.ReactElement
}

const Libf0b0 = (props: Props) => {
  const [file, setFile] = React.useState<string>('');
  const [language, setLanguage] = React.useState<string>('中文');

  const handleUploadSuccess = (file_path: string) => {
    setFile(file_path);
  };

  const handleCommit = () => {
    const all_data_should_save = {
      file: file,
      language: language
    };
    props.save_data(all_data_should_save);
  };

  return (
    <>
      <props.FileUploadConponent onUploadSuccess={handleUploadSuccess} title='' />
      <antd.Select defaultValue="中文" onChange={(value: string) => setLanguage(value)}>
        <antd.Select.Option value="中文">中文</antd.Select.Option>
        <antd.Select.Option value="日文">日文</antd.Select.Option>
        <antd.Select.Option value="英文">英文</antd.Select.Option>
      </antd.Select>
      <antd.Button type="primary" onClick={handleCommit}>提交</antd.Button>
    </>
  );
};

export default Libf0b0;